
<script>

	export let id = ''
	export let disabled = false
	export let activeItem = false

	$: isActive = activeItem === id
	$: activeClass = isActive ? 'active' : ''

	const activate = () => {

		if (disabled) {
			return
		}

		activeItem = id
	}

</script>

<li class="tabs_item {activeClass}" {disabled} on:click="{activate}">
	<slot />
</li>


<style>

	.tabs_item {
		display: inline-block;
		cursor: pointer;
		padding: 5px 10px;
		/*border-radius: 4px;*/
		border-bottom: 2px solid var(--tabs_container_color, #000);
	}

	.tabs_item.active {
		border: none;
		border-radius: 4px;
		color: var(--sc_theme_active_color_01, #fff);
		background: var(--sc_theme_active_background_01, #ff3e00);
	}

	.tabs_item[disabled=true] {
		cursor: default;
		opacity: .3;
	}

</style>