
const typesOfModal = {

	medium: {
		'--margin-top' : '20px',
		'--width' : 'min(1000px, 100%)',
	},

	full_screen: {
		'--margin-top' : '0px',
		'--width' : '100%',
		'height': '100%',
	},

	large: {
		'--width' : '95%',
		'--margin-top' : '20px',
		'max-width' : '1400px',
	},

	medium_small: {
		'--margin-top' : '40px',
		'--width' : 'min(600px, 100%)',
	},

	small: {
		'--margin-top' : '40px',
		'--width' : 'min(400px, 100%)',
	}
}

typesOfModal.default = typesOfModal.medium

export const addTypeOfModal = (name, args) => {

	if (name?.constructor !== String) {
		throw 'Name must be a string.'
	}

	if (args?.constructor !== Object) {
		throw 'Name must be a object.'
	}

	typesOfModal[name] = args
}

export const getTypesOfModal = () => {
	return typesOfModal
}






















export const setHTMlOverFlow = (visible) => {

	const html = document.querySelector('html')

	if (visible) {
		html.style.overflow = 'hidden';
	}
	else{
		html.style.overflow = '';
	}
}

export const getDefaulModalID = () => {

	const length = 20
	const caracter = 'QWERTYUIOPASDFGHJKLZXCVBNMqwertyuiopasdfghjklzxcvbnm1234567890'

	let result = ''

	for(let i = 0; i < length; i++) {
		let index = parseInt(Math.random() * (length - 1))

		result += caracter[index]
	}

	return result
}

export const getModalType = (modalType) =>  {
	const _typesOfModal = getTypesOfModal()
	return _typesOfModal[modalType] || _typesOfModal['default']
}

export const getStylesByModalType = (modalType) =>  {

	const _modalType = getModalType(modalType)

	let result = ''

	for (const key in _modalType) {
		result += `${key}: ${_modalType[key]};`
	}

	return result
}


export const getAlign = (align) => {

	if (align === 'right' || align === 'left') {
		return align
	}

	return 'center'
}




export const getVerticalAlign = (align) => {

	if (align === 'middle' || align === 'bottom') {
		return align
	}

	return 'top'
}