<script>

	import { useToast } from '../../../package/index.js'
	import { toastCode } from './data'
	import CodeDocumentation from '../components/codeDocumentation.svelte'

	const toast = () => {

		useToast({
			type: 'success',
		})('Message')

		useToast({
			type: 'info',
		})('Message')

		useToast({
			type: 'warning',
		})('Message')

		useToast({
			type: 'danger',
		})('Message')

		useToast({
			type: 'dark',
		})('Message')

		useToast({
			type: 'light',
		})('Message')

	}

</script>

<CodeDocumentation
	{...toastCode}
	run={toast}
/>


