<script>

	import { getCode, copyTextToClipboard } from './controller.js'
	import { onMount } from 'svelte'
	import CodeFlask from 'codeflask'

	export let run = () => {}
	export let args = {}
	export let code = ''
	export let title = ''
	export let description = ''
	export let code_height = ''

	args

	let editorElement = null

	onMount(() => {

		const flask = new CodeFlask(editorElement, { language: 'js', readonly: true })

		flask.updateCode(getCode(code))
	})

</script>

<div class="code_documentation">

	<section class="title">
		{title}
	</section>

	<section class="description">
		{@html description}
	</section>

	<section class="controllers">
		<button on:click={run}>Run</button>
		<button on:click={() => copyTextToClipboard(code)} >Copy</button>
	</section>

	<section class="editor" style="height: {code_height};" bind:this={editorElement} />

</div>

<style>
	
	.code_documentation {
		flex-direction: column;
		display: flex;
		grid-gap: 10px;
		margin: 0px auto 30px;
		width: min(600px, 100%);
	}

	.title {
		font-weight: bold;
		font-size: 1.1rem;
	}

	.description {
		font-style: italic;
		font-size: 0.85rem;
	}

	.controllers {
		justify-content: right;
		display: flex;
		grid-gap: 10px;
		margin: 0px 0px -5px;
	}

	.controllers button {
		font-size: .87rem;
	}

	.editor {
		position: relative;
		width: 100%;
		height: 205px;
	}

	.editor :global(textarea) {
		color: transparent;
	}
	
	.editor :global(.codeflask) {
		background: var(--secondaryBGColor);
		border-radius: 5px;
		box-shadow: 0 0 10px rgb(0 0 0 / 10%);
	}

</style>