<script>

	import { setHTMlOverFlow, getAlign, getStylesByModalType, getVerticalAlign } from './_controller.js'
	import { onMount } from 'svelte';

	export let show = false
	export let align = null
	export let onShow = null
	export let complete = false
	export let verticalAlign = null
	export let verticalMargin = null
	export let modalType = 'default'
	export let allowCloseOnSurfaceElement = true
	export let style = ''

	let modalElement = null
	let surfaceElement = null

	$: show + onChangeState()
	$: _style = `
		${getStylesByModalType(modalType)}
		${style}
		${verticalMargin && modalType !== 'full_screen' ? `--margin-top: ${verticalMargin};` : ''}
	`


	$: alignClass = getAlign(align)
	$: verticalAlignClass = getVerticalAlign(verticalAlign)
	$: showClass = show ? 'show' : 'hide'

	const onChangeState = () => {

		setHTMlOverFlow(show)

		if (show && typeof onShow === 'function') {
			onShow()
		}
	}

	const closeModal = (e) => {
		if (allowCloseOnSurfaceElement) {
			show = false
		}
	}

	onMount(() => {
		modalElement.onscroll = () => {
			surfaceElement.style.top = modalElement.scrollTop + 'px'
		}
	})

</script>

<div class="sc_modal {showClass} {alignClass} {verticalAlignClass}" bind:this={modalElement} >

	<div class="sc_modal--surface" on:click="{closeModal}" bind:this={surfaceElement} ></div>

	<div class="sc_modal--container {complete ? 'complete' : ''}" style={_style}>
		<slot />
	</div>

</div>

<style>

	* {
		box-sizing: border-box;
	}

	:root {
		--sc_theme_color_01 : #000;
		--sc_theme_background_01 : #fff;
		--sc_theme_background_modal_surface: rgba(0, 0, 0, .7);
	}

	.sc_modal {
		position: fixed;
		flex-wrap: wrap;
		overflow: auto;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1000;
	}

	.sc_modal.show {
		display: flex;
	}

	.sc_modal.hide {
		display: none;
	}

	.sc_modal.center {
		justify-content: center;
	}

	.sc_modal.right {
		justify-content: flex-end;
	}

	.sc_modal.left {
		justify-content: flex-start;
	}

	.sc_modal.middle {
		align-items: center;
	}

	.sc_modal.top {
		align-items: flex-start;
	}

	.sc_modal.bottom {
		align-items: flex-end;
	}

	.sc_modal--surface {
		position: absolute;
		top: 0px;
		left: 0px;
		width: 100%;
		height: 100%;

		background: var(--sc_theme_background_modal_surface);

		-webkit-backdrop-filter: blur(2px);
		backdrop-filter: blur(2px);
	}

	.sc_modal--container {
		flex-direction: column;
		overflow: hidden;
		display: flex;
		z-index: 1;
		border-radius: 4px;
		width: var(--width);
		margin-top: var(--margin-top);
		margin-bottom: var(--margin-top);
		box-shadow: 0px 10px 20px 0px rgb(0 0 0 / 20%);
		background: var(--sc_theme_background_01);
		color: var(--sc_theme_color_01);
	}

	.sc_modal--container.complete {
		height: calc(100% - (var(--margin-top) * 2));
	}

</style>