
<script>

	import TabItem from './TabItem.svelte'
	import { setScrollLeft } from './_controller.js'
	import { onMount } from 'svelte'

	export let activeItem = null
	export let className = ''
	export let style = ''
	export let tabs = []

	let ulElement = null
	let arrowLeft = null
	let arrowRight = null

	let allowArrows = false

	const checkResize = () => {
		allowArrows = ulElement ? ulElement.scrollWidth > ulElement.offsetWidth : false
	}

	$: if (!allowArrows && ulElement) {
		arrowLeft.style.display = 'none'
		arrowRight.style.display = 'none'
	}else if (ulElement) {
		arrowLeft.style.display = ''
		arrowRight.style.display = ''
	}

	onMount(() => {

		checkResize()
		window.addEventListener("resize", () => checkResize())

		arrowLeft.onclick = () => {
			setScrollLeft(ulElement, false)
		}

		arrowRight.onclick = () => {
			setScrollLeft(ulElement, true)
		}
	})

</script>

<div class="tabs_container {className}" {style} >

	<span class="arrow left" bind:this={arrowLeft}>
		{'<'}
	</span>

	<ul bind:this={ulElement} >

		{#each tabs as tab, index}
			<TabItem disabled={tab?.disabled} bind:activeItem id={tab?.id || tab?.text || tab}>
				{tab?.text || tab || (index + 1)}
			</TabItem>
		{/each}

	</ul>

	<div class="arrow right" bind:this={arrowRight}>
		{'>'}
	</div>
</div>

<style>

	.tabs_container {
		position: relative;
		display: flex;
		border-radius: 4px;
		padding: 10px 15px;
		color: var(--tabs_container_color, #000);
		background: var(--tabs_container_background, #efefef);
	}


	.tabs_container ul {
		list-style-type: none;
		white-space: pre;
		overflow: auto;
		display: flex;
		flex: auto;
		margin: 0px;
		padding: 0px;
		grid-gap: 20px;
	}


	.tabs_container ul::-webkit-scrollbar {
		display: none;
	}

	.arrow {
		align-items: center;
		font-weight: bold;
		cursor: pointer;
		display: flex;
	}

	.arrow.left {
		margin-right: 15px;
	}

	.arrow.right {
		margin-left: 15px;
	}

</style>